import React from 'react'
import Navbar from '../navbar/Navbar'
import "./header.css"

const Header = ({setShowMain, setShowBooking, setShowPricing, setShowRules, setShowSmaller, setShowBigger, setShowRehearsal}) => {
  
    return (
    <div className="header" id="head">
        <div className="header-title" onClick={()=>{
                     setShowBooking(false)
                     setShowMain(true)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowRehearsal(false)
                }}>
            <h1>Próbówka Music House</h1>
            <h4>sale prób i studio nagrań</h4>
        </div>
        <Navbar 
          setShowMain={setShowMain} setShowBooking={setShowBooking} setShowPricing={setShowPricing} 
          setShowRules={setShowRules} setShowSmaller={setShowSmaller} setShowBigger={setShowBigger}
          setShowRehearsal={setShowRehearsal}
        />
        
    </div>
    )
}

export default Header
