import React, {useState, useEffect} from 'react'
import Header from './components/header/Header'
import MainContainer from './components/main/MainContainer'
import Booking from './components/booking/Booking'
import Footer from './components/footer/Footer'
import Rules from './components/rules/Rules'
import Pricing from './components/pricing/Pricing'
import Bigger from './components/bigger/Bigger'
import Smaller from './components/smaller/Smaller'
import Rehearsal from './components/rehearsal/Rehearsal'

const App = () => {

  const [showMain, setShowMain] = useState(true)
  const [showBooking, setShowBooking] = useState(false)
  const [showPricing, setShowPricing] = useState(false)
  const [showRules, setShowRules] = useState(false)
  const [showBigger, setShowBigger] = useState(false)
  const [showSmaller, setShowSmaller ] = useState(false)
  const [showRehearsal, setShowRehearsal] = useState(false)

  useEffect(() => {
    const header = document.getElementById("head");
    const sticky = header.offsetTop;
    const scrollCallBack = window.addEventListener("scroll", () => {
      if (window.pageYOffset > sticky) {
        header.classList.add("sticky");
        } else {
        header.classList.remove("sticky");
        }
    });
    return () => {
      window.removeEventListener("scroll", scrollCallBack);
    };
  }, []);
  return (
    <div className="main">
      <Header 
      setShowMain={setShowMain} 
      setShowBooking={setShowBooking} 
      setShowPricing={setShowPricing} 
      setShowRules={setShowRules} 
      setShowSmaller={setShowSmaller}
      setShowBigger={setShowBigger}
      setShowRehearsal={setShowRehearsal}
      />
      {showMain &&
      <MainContainer 
      setShowMain={setShowMain} 
      setShowBooking={setShowBooking} 
      setShowPricing={setShowPricing} 
      setShowRules={setShowRules} 
      setShowSmaller={setShowSmaller}
      setShowBigger={setShowBigger}
      setShowRehearsal={setShowRehearsal}
      />
      }
      {showBooking &&
      <Booking />
      }
      {showRules &&
      <Rules/>
      }
      {showPricing &&
      <Pricing />
      }
      {showSmaller &&
      <Smaller/>
      }
      {showBigger && 
      <Bigger/>
      }
      {showRehearsal &&
      <Rehearsal/>
      }
      <Footer/>
    </div>
  )
}

export default App
