import React from 'react'
import Button from './Button'

const Book = ({setShowMain, setShowBooking, setShowPricing, setShowRules, setShowSmaller, setShowBigger, setShowRehearsal}) => {
    return (
        <div className="about-us-booking">
                <div className="about-us-booking-header"><h2>Profesjonalne sale prób</h2></div>
                <div className="about-us-booking-content" >
                    <Button text="Rezerwacje" onClick={()=>{
                     setShowBooking(true)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowRehearsal(false)
                }}/>
                    <Button text="Cennik" onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(true)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowRehearsal(false)
                }}/>
                </div>
            </div>
    )
}

export default Book
