import React from 'react'
import './navbar.css'
import NavbarLink from './navlink/NavbarLink'

const Navbar = ({setShowMain, setShowBooking, setShowPricing, setShowRules, setShowBigger, setShowSmaller, setShowRehearsal}) => {
    return (
        <div className="navbar">
            <div className="navbar-inside">
                <NavbarLink text={"Rezerwacje"} color={setShowBooking ? "pink" : "orange"} onClick={()=>{
                    setShowBooking(true)
                    setShowMain(false)
                    setShowPricing(false)
                    setShowRules(false)
                    setShowBigger(false)
                    setShowSmaller(false)
                    setShowRehearsal(false)
                }}/>
                <NavbarLink text={"Sala mniejsza"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(true)
                     setShowRehearsal(false)
                }}/>
                <NavbarLink text={"Sala większa"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(true)
                     setShowSmaller(false)
                     setShowRehearsal(false)
                }}/>
                <NavbarLink text={"Studio"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowRehearsal(true)
                }}/>
                <NavbarLink text={"Cennik"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(true)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowRehearsal(false)
                }}/>
                <NavbarLink text={"Regulamin"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(true)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowRehearsal(false)
                }}/>
                
            </div>
        </div>
    )
}

export default Navbar
